<template>
    <div class='zhaohuo'>
        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane :label="$t('zhaohuoTab[0]')" name="fabu">
                <div class="fabuForm">
                    <div class="fabuItem">
                        <div>{{$t('zhaohuo[2]')}}：</div>
                        <div>
                            <el-input v-model="formdata.county" :placeholder="$t('zhaohuo[0]')"></el-input>
                        </div>
                    </div>
                    <div class="fabuItem">
                        <div>{{$t('zhaohuo[3]')}}：</div>
                        <div>
                            <el-input v-model="formdata.brand" :placeholder="$t('zhaohuo[0]')"></el-input>
                        </div>
                    </div>
                    <div class="fabuItem">
                        <div>{{$t('zhaohuo[4]')}}：</div>
                        <div class="typeSelect">
                            <div>
                                <el-select v-model="catyValue1" :placeholder="$t('zhaohuo[1]')" @change="select1()">
                                    <el-option
                                    v-for="item in caty1"
                                    :key="item.id"
                                    :label="item.category_name"
                                    :value="item.id">
                                    </el-option>
                                </el-select>
                            </div>
                            <div>
                                <el-select v-model="catyValue2" :placeholder="$t('zhaohuo[1]')" @change="select2()">
                                    <el-option
                                    v-for="item in caty2"
                                    :key="item.id"
                                    :label="item.category_name"
                                    :value="item.id">
                                    </el-option>
                                </el-select>
                            </div>
                            <div>
                                <el-select v-model="catyValue3" :placeholder="$t('zhaohuo[1]')" @change="select3()">
                                    <el-option
                                    v-for="item in caty3"
                                    :key="item.id"
                                    :label="item.category_name"
                                    :value="item.id">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                    </div>
                    <div class="fabuItem">
                        <div>{{$t('zhaohuo[5]')}}：</div>
                        <div>
                            <el-input v-model="formdata.name" :placeholder="$t('zhaohuo[0]')"></el-input>
                        </div>
                    </div>
                    <div class="fabuItem">
                        <div>{{$t('zhaohuo[6]')}}：</div>
                        <div>
                            <el-input v-model="formdata.sku" :placeholder="$t('zhaohuo[0]')"></el-input>
                        </div>
                    </div>
                    <div class="fabuItem">
                        <div>{{$t('zhaohuo[7]')}}：</div>
                        <div class="typeSelect1">
                            <div><el-input v-model="minprice" type="Number" min="0"></el-input></div>
                            <div>－</div>
                            <div><el-input v-model="maxPrice" type="Number" min="0"></el-input></div>
                        </div>
                    </div>
                    <div class="fabuItem miaoshu">
                        <div>{{$t('zhaohuo[8]')}}：</div>
                        <div>
                            <el-input
                                resize='none'
                                type="textarea"
                                :placeholder="$t('zhaohuo[0]')"
                                v-model="formdata.desc">
                            </el-input>
                        </div>
                    </div>
                    <div class="fabuItem shangchuan">
                        <div>{{$t('zhaohuo[9]')}}：</div>
                        <div class="uploader">
                            <el-upload
                                class="avatar-uploader"
                                action="http://192.168.1.113/v1/upload_file"
                                :show-file-list="false"
                                name='image'
                                :on-success="handleAvatarSuccess"
                                :before-upload="beforeAvatarUpload">
                                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                            </el-upload>
                        </div>
                    </div>
                    <div class="fabuBtn" @click="fabuxuqiu()">{{$t('zhaohuo[10]')}}</div>
                </div>
            </el-tab-pane>
            <el-tab-pane :label="$t('zhaohuoTab[1]')" name="lishi">
                <div class="lishi">
                    <el-tabs v-model="activeName1" @tab-click="handleClick1">
                        <el-tab-pane :label="$t('zhaohuoTab[2]')" name="a">
                            <div class="dingdanList">
                                <div v-if="get_find_list.lenght <= 0" class="zanwu">
                                    <img src="../../../assets/img/shangcheng/tubiao.png" alt="">
                                    <h1>{{$t('zhaohuo[11]')}}...</h1>
                                </div>
                                <div class="list" v-for="(item,i) in get_find_list" :key="i">
                                    <div class="listTitle">
                                        <div>
                                            <!-- <span></span> -->
                                            {{item.time}}
                                        </div>
                                        <div>
                                        </div>
                                        <div>
                                            {{item.status | zhuangtai}}
                                        </div>
                                    </div>
                                    <div class="item">
                                        <div>
                                            <div class="img">
                                                <img :src="imgUrl + item.image" alt="">
                                            </div>
                                            <div class="text">
                                                <h1>{{item.name}}</h1>
                                                <p>{{item.desc}}</p>
                                            </div>
                                        </div>
                                        <div>
                                            {{item.sku}}
                                        </div>
                                        <div>{{item.county}}</div>
                                        <div></div>
                                        <div>¥{{item.sell_price}}</div>
                                        <div>
                                            <div class="quxiadan" v-if="item.status == 2" @click="quxiadan(item.id)">{{$t('zhaohuo[12]')}}</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane :label="$t('zhaohuoTab[3]')" name="3">
                            <div class="dingdanList">
                                <div v-if="get_find_list.lenght <= 0" class="zanwu">
                                    <img src="../../../assets/img/shangcheng/tubiao.png" alt="">
                                    <h1>{{$t('zhaohuo[11]')}}...</h1>
                                </div>
                                <div class="list" v-for="(item,i) in get_find_list" :key="i">
                                    <div class="listTitle">
                                        <div>
                                            <!-- <span></span> -->
                                            {{item.time}}
                                        </div>
                                        <div>
                                        </div>
                                        <div>
                                            {{item.status | zhuangtai}}
                                        </div>
                                    </div>
                                    <div class="item">
                                        <div>
                                            <div class="img">
                                                <img :src="imgUrl + item.image" alt="">
                                            </div>
                                            <div class="text">
                                                <h1>{{item.name}}</h1>
                                                <p>{{item.desc}}</p>
                                            </div>
                                        </div>
                                        <div>
                                            {{item.sku}}
                                        </div>
                                        <div>{{item.county}}</div>
                                        <div></div>
                                        <div>¥{{item.sell_price}}</div>
                                        <div></div>
                                    </div>
                                </div>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane :label="$t('zhaohuoTab[4]')" name="2">
                            <div class="dingdanList">
                                <div v-if="get_find_list.lenght <= 0" class="zanwu">
                                    <img src="../../../assets/img/shangcheng/tubiao.png" alt="">
                                    <h1>{{$t('zhaohuo[11]')}}...</h1>
                                </div>
                                <div class="list" v-for="(item,i) in get_find_list" :key="i">
                                    <div class="listTitle">
                                        <div>
                                            <!-- <span></span> -->
                                            {{item.time}}
                                        </div>
                                        <div>
                                        </div>
                                        <div>
                                            {{item.status | zhuangtai}}
                                        </div>
                                    </div>
                                    <div class="item">
                                        <div>
                                            <div class="img">
                                                <img :src="imgUrl + item.image" alt="">
                                            </div>
                                            <div class="text">
                                                <h1>{{item.name}}</h1>
                                                <p>{{item.desc}}</p>
                                            </div>
                                        </div>
                                        <div>
                                            {{item.sku}}
                                        </div>
                                        <div>{{item.county}}</div>
                                        <div></div>
                                        <div>¥{{item.sell_price}}</div>
                                        <div>
                                            <div class="quxiadan" v-if="item.status == 2" @click="quxiadan(item.id)">{{$t('zhaohuo[12]')}}</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane :label="$t('zhaohuoTab[5]')" name="1">
                            <div class="dingdanList">
                                <div v-if="get_find_list.lenght <= 0" class="zanwu">
                                    <img src="../../../assets/img/shangcheng/tubiao.png" alt="">
                                    <h1>{{$t('zhaohuo[11]')}}...</h1>
                                </div>
                                <div class="list" v-for="(item,i) in get_find_list" :key="i">
                                    <div class="listTitle">
                                        <div>
                                            <!-- <span></span> -->
                                            {{item.time}}
                                        </div>
                                        <div>
                                        </div>
                                        <div>
                                            {{item.status | zhuangtai}}
                                        </div>
                                    </div>
                                    <div class="item">
                                        <div>
                                            <div class="img">
                                                <img :src="imgUrl + item.image" alt="">
                                            </div>
                                            <div class="text">
                                                <h1>{{item.name}}</h1>
                                                <p>{{item.desc}}</p>
                                            </div>
                                        </div>
                                        <div>
                                            {{item.sku}}
                                        </div>
                                        <div>{{item.county}}</div>
                                        <div></div>
                                        <div>¥{{item.sell_price}}</div>
                                        <div>
                                            <div class="quxiadan" v-if="item.status == 2" @click="quxiadan(item.id)">{{$t('zhaohuo[12]')}}</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </div>
                <div>
            <div class="fenye">
                    <el-pagination
                        @current-change="handleCurrentChange"
                        :page-size="pageSize"
                        layout="prev, pager, next, jumper"
                        :current-page.sync="sync"
                        :total="pageTptal">
                    </el-pagination>
                </div>
                <div></div>
            </div>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>
<script>
export default {
    data() {
        return {
            pageSize:7,//每页显示条数
            pageTptal:1,//总条数
            sync:1,//当前页数
            imgimg:'',
            activeName:'fabu',
            activeName1:'a',
            
            input:'',
            value: '',
            textarea:'',


            caty1:'',
            caty2:'',
            caty3:'',
            catyValue1:'',
            catyValue2:'',
            catyValue3:'',
            imageUrl: '',
            minprice:'',
            maxPrice:'',
            formdata:{
                county:'',
                brand:'',
                category_id:'',
                name:'',
                sku:'',
                price:'',
                desc:'',
                image:'',
            },
            get_find_list:'',//找貨列表
        };
    },
    computed: {
        price: function() {
            return this.minprice + '-' + this.maxPrice
        }
    },
    filters: {
        zhuangtai:function(value){
            if(value == 1){
                return '已完成';
            }
            if(value == 2){
                return '已有货源';
            }
            if(value == 3){
                return '无货源';
            }
        },
    },
    created:function(){
    },
    mounted:function(){
        var _this = this;
        _this.imgUrl = _this.apis.second;
    },
    watch:{
    },
    methods:{
        handleClick(){
            var _this = this;
            console.log(_this.activeName)
            if(_this.activeName == 'lishi'){
                _this.getList(1)
            }
        },
        handleCurrentChange(val) {
            var _this = this;
            _this.getList(val);
        },
        handleClick1(){
            var _this = this;
            _this.getList(1)
        },
        getList(val){
            var _this = this;
            if(_this.activeName1 == 'a'){
                var status = '' 
            }else{
                var status = _this.activeName1
            }
            _this.apis.get(_this.apis.get_find_list,{params:{lang:_this.apis.lang,page:val,pageNumber:_this.pageSize,status:status}}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.get_find_list = res.data.data.data
                    _this.pageTptal = res.data.data.total
                    _this.sync = res.data.data.current_page
                }
            })
        },
        //发布需求
        fabuxuqiu(){
            var _this = this;
            _this.formdata.price = _this.minprice + '-' + _this.maxPrice
            _this.apis.post(_this.apis.find_goods,_this.formdata).then(function(res) {
                if(res.data.code == 0){
                    _this.get_first_category = res.data.data
                    _this.$message({
                        message: res.data.msg,
                        type: 'warning'
                    });
                    _this.activeName = 'lishi'
                }else{
                    _this.$message({
                        message: res.data.msg,
                        type: 'warning'
                    });
                }
            })
        },
        handleAvatarSuccess(res, file) {
            this.imageUrl = URL.createObjectURL(file.raw);
            this.formdata.image = res.data
        },
        beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg';
            const isLt2M = file.size / 1024 / 1024 < 2;
            if (!isJPG) {
                this.$message.error('上传头像图片只能是 JPG 格式!');
            }
            if (!isLt2M) {
                this.$message.error('上传头像图片大小不能超过 2MB!');
            }
            return isJPG && isLt2M;
        },
        select1(){
            var _this = this
            _this.apis.get(_this.apis.get_next_category,{params:{lang:_this.apis.lang,id:_this.catyValue1}}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.caty2 = res.data.data
                }
            })
        },
        select2(){
            var _this = this
            _this.apis.get(_this.apis.get_next_category,{params:{lang:_this.apis.lang,id:_this.catyValue2}}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.caty3 = res.data.data
                }
            })
        },
        select3(){
            var _this = this
            console.log(_this.catyValue3)
            _this.formdata.category_id = _this.catyValue3
        },
        //{{$t('zhaohuo[11]')}}
        quxiadan(id){
            var _this = this;
            _this.$router.push({
                path:'xiangqing',
                query:{id:id,title:'商品详情'}
            })
        },
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
.zhaohuo{
    .fabuForm{
        min-height: 800px;
        margin-bottom: 70px;
        padding-left: 40px;
        .fabuItem{
            height: 40px;
            line-height: 40px;
            margin: 28px 0;
            display: flex;
            align-items: center;
            &>div:nth-child(1){
                width: 94px;
                margin-right: 20px;
                font-weight: bold;
                color: #383838;
            }
            &>div:nth-child(2){
                width: 344px;
                height: 100%;
            }
            .typeSelect{
                flex: 1;
                display: flex;
                &>div{
                    margin-right: 20px;
                }
            }
            .typeSelect1{
                flex: 1;
                display: flex;
                &>div:nth-child(2){
                    width: auto;
                }
                &>div{
                    width: 80px;
                    margin-right: 20px;
                }
            }
            .uploader{
                
            }
        }
        .miaoshu,.shangchuan{
            height:auto !important;
        }
        .fabuBtn{
            font-size: 15px;
            color: #fff;
            width: 126px;
            height: 32px;
            margin: 70px auto;
            text-align: center;
            line-height: 32px;
            background: #FFAD01;
        }
    }
    .fenye{
        margin-bottom: 30px;
        .el-pagination{
            text-align: right;
        }
    }
    .lishi{
        padding-left: 40px;
        padding-top: 20px;
        .dingdanList{
            width: 1020px;
            margin-bottom: 10px;
            border-bottom: 1px solid #dadada;
            .dingdanTitle{
                display: flex;
                align-items: center;
                text-align: center;
                border-bottom: 2px solid #FFAE02;
                margin-bottom: 20px;
                padding: 0 22px;
                &>div{
                    height: 32px;
                    line-height: 32px;
                }
                &>div:nth-child(1){
                    width: 410px;
                }
                &>div:nth-child(2){
                    width: 120px;
                }
                &>div:nth-child(3){
                    width: 100px;
                }
                &>div:nth-child(4){
                    width: 150px;
                }
                &>div:nth-child(5){
                    width: 120px;
                }
                &>div:nth-child(6){
                    flex: 1;
                }
            }
            .shanchu{
                line-height: 20px;
                text-align: right;
                span{
                    vertical-align: middle;
                    cursor:pointer;
                }
                .quanxuan{
                    display: inline-block;
                    width: 14px;
                    height: 14px;
                    margin-right: 5px;
                    border: 1px solid #C7C7C7;
                }
                .shanchuIcon{
                    display: inline-block;
                    width: 14px;
                    height: 14px;
                    margin-right: 5px;
                    border: 1px solid #C7C7C7;
                }
            }
            .list{
                background: #FFF2EC;
                margin: 20px 0;
                padding: 14px 0;
                .listTitle{
                    display: flex;
                    align-items: center;
                    padding: 0 22px;
                    border-bottom: 1px solid #C7C7C7;
                    &>div{
                        height: 33px;
                        line-height: 33px;
                    }
                    &>div:nth-child(1){
                        line-height: 33px;
                        color: #000000;
                        font-size: 13px;
                        font-weight: bold;
                        margin-right: 15px;
                        span{
                            display: inline-block;
                            width: 14px;
                            height: 14px;
                            margin-right: 10px;
                            background: #fff;
                            border: 1px solid #C7C7C7;
                            vertical-align: middle;
                        }
                    }
                    &>div:nth-child(2){
                        
                    }
                    &>div:nth-child(3){
                        flex: 1;
                        text-align: right;
                        font-size: 13px;
                    }
                }
                .item{
                    display: flex;
                    align-items: center;
                    text-align: center;
                    padding: 10px 22px;
                    &>div:nth-child(1){
                        width: 410px;
                        display: flex;
                        align-items: top;
                        .img{
                            width: 84px;
                            height: 84px;
                            margin-right: 16px;
                        }
                        .text{
                            h1{
                                font-size: 13px;
                            }
                            p{
                                font-size: 12px;
                                color: #959595;
                            }
                        }
                    }
                    &>div:nth-child(2){
                        width: 120px;
                        &>div:nth-child(1){
                            text-decoration:line-through;
                            color: #8F8F8F;
                        }
                        &>div:nth-child(2){
                            font-size: 13px;
                        }
                    }
                    &>div:nth-child(3){
                        width: 100px;
                    }
                    &>div:nth-child(4){
                        width: 150px;
                        color: #8F8F8F;
                    }
                    &>div:nth-child(5){
                        width: 120px;
                    }
                    &>div:nth-child(6){
                        flex: 1;
                        .quxiadan{
                            font-size: 16px;
                            color: #fff;
                            display: inline-block;
                            width: 86px;
                            height: 34px;
                            text-align: center;
                            line-height: 34px;
                            margin-right: 0;
                            background: #FFAD01;
                            border-radius: 100px;
                        }
                    }
                }
            }
        }
    }
}
.zanwu{
    margin: 0 auto;
    text-align: center;
    img{
        width: 338px;
        height: 306px;
    }
    h1{
        color: #9e9e9e;
        font-size: 22px;
        line-height: 50px; 
        margin: 10px 0;
    }
}
</style>


